<template>
  <div>
    <ul>
      <li v-for="(item, index) in list"
          :key="index">
        <router-link :to="{name:'detail',query:{obj:item}}">{{item.title}}</router-link>
        <span>{{item.createtime|dateformat}}</span>
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  props: {
    list: Array
  }
}
</script>
<style lang="scss" scoped>
ul {
  padding: 20px;
  li {
    width: 840px;
    line-height: 54px;
    height: 54px;
    text-overflow: ellipsis;
    color: #333333;
    font-size: 16px;
    line-height: 26px;
    display: flex;
    justify-content: space-between;
    border-bottom: dashed 1px #cccccc;
    align-items: center;
    &:first-child {
      border-top: dashed 1px #cccccc;
    }
    span {
      padding-right: 30px;
    }
  }
}
</style>



